.SingleCoupon {
    margin: 0.7vh;
    height: 20vh;
    aspect-ratio: 5/4;
    display: grid;
	border: thin solid rgb(244, 244, 244);
    border-radius: 5%;
    background-color: white;
    color: black;
    grid-template-areas: "image image image"
                         "title title price"
                         "desc desc desc"
                         "category purch last";
    grid-template-columns: 40% 30% 30%;
    grid-template-rows: 50% 15% 20% 15%;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    cursor: pointer;
    box-shadow: rgb(0 0 0 / 5%) 0px 0.2vh 0.5vh;
    text-decoration: none;
    overflow: hidden;
}
.SingleCoupon:hover{
    box-shadow: rgb(0 0 0 / 10%) 0px 0.2vh 0.5vh;
}
.Image{
    grid-area: image;
    background-color: antiquewhite;
    overflow: hidden;
}
.Title{
    grid-area: title;
    text-align: left;
    font-size: 2vh;
}
.Price{
    grid-area: price;
    text-align: right;
    color: green;
    font-size: 2vh;
}
.Description{
    grid-area: desc;
    text-align: left;
    font-size: 1.5vh;
}
.Category{
    grid-area: category;
    font-size: 1vh;
    text-align: left;
}
.Purchase{
    grid-area: purch;
    align-items: center;
    justify-items: center;
}
.Purchase>button{
    display: inline-flex;
    height: 2vh;
    aspect-ratio: 6/2;
    border-radius: 10%;
    border: transparent;
    background-color: black;
    color: white;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 500ms ease 0s, border-color 500ms ease 0s, color 500ms ease 0s, fill 500ms ease 0s;
    text-decoration: none;
    font-size: 0.8vh;
}
.Purchase>button:hover{
    background-color: rgb(100, 100, 100);
    border: transparent;
}
.RemoveFromCart{
    grid-area: purch;
    align-items: center;
    justify-items: center;
}
.RemoveFromCart>button{
    display: inline-flex;
    height: 2vh;
    aspect-ratio: 6/2;
    border-radius: 10%;
    border: transparent;
    background-color: rgb(120, 0, 0);
    color: white;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 500ms ease 0s, border-color 500ms ease 0s, color 500ms ease 0s, fill 500ms ease 0s;
    text-decoration: none;
    font-size: 1vh;
}
.RemoveFromCart>button:hover{
    background-color: rgb(100, 100, 100);
    border: transparent;
}
.Last{
    grid-area: last;
    font-size: 1vh;
    color: red;
    align-items: center;
    justify-items: center;
}
.Last>button{
    display: inline-flex;
    height: 2vh;
    aspect-ratio: 5/2;
    border-radius: 10%;
    border: transparent;
    background-color: rgb(0, 0, 120);
    color: white;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 500ms ease 0s, border-color 500ms ease 0s, color 500ms ease 0s, fill 500ms ease 0s;
    text-decoration: none;
    font-size: 1vh;
}
.Last>button:hover{
    background-color: rgb(100, 100, 100);
    border: transparent;
}