.AllCoupons {
	height: 100%;
    width: 100%;
    overflow: hidden;
    grid-template-rows: 15vh 60vh;
}
.FilterBar{
    grid-row: 1/span 1;
}
.CouponContainer{
    border: 0.2vw black solid;
    border-radius: 1vh;
    margin: 1vh 1vw;
    box-shadow: black 2vh 2vh 4vh;
    grid-row: 2/span 1;
    margin-left: 5vw;
    width: 90vw;
    height: 55vh;
    align-items: center;
    overflow: auto;
}
.CouponContainer>span{
    display: inline-block;
}