.AboutUsButton {
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}
.AboutUsButton>button{
    margin-top: 1.5vh;
    font-family: "Lucida Handwriting","Papyrus","Monaco";
    display: inline-flex;
    height: 7vh;
    aspect-ratio: 7/5;
    border-radius: 20%;
    border: transparent;
    background-color: black;
    color: white;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 500ms ease 0s, border-color 500ms ease 0s, color 500ms ease 0s, fill 500ms ease 0s;
    font-size: 2vh;
}
.AboutUsButton>button:hover{
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.16), rgba(255, 255, 255, 0.16)), rgb(30, 30, 30);
    border: transparent;
}