.MyCart{
	height: 100%;
    width: 100%;
    display: grid;
    overflow: hidden;
    background-color: rgb(210, 210, 210);
}
.TheCart{
    height: 65vh;
    width: 95vw;
    display: grid;
    grid-template-rows: 10vh 55vh;
    background-color: white;
    margin: 5vh 2.5vw;
    align-items: center;
}
.CartTitle{
    align-items: center;
    grid-row: 1/span 1;
    display: grid;
    grid-template-columns: 19vw 9.5vw 57vw 9.5vw;
    border-bottom: 0.3vh black solid;
}
.Head{
    align-items: center;
    grid-column: 1/span 1;
    display: inline-block;
    text-align: left;
    margin-left: 1vw;
    font-size: 4vh;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.ItemCounter{
    font-size: 3vh;
    align-items: center;
    grid-column: 2/span 1;
    display: inline-block;
    text-align: left;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.TotalPrice{
    grid-column: 4/span 1;
    text-align: right;
    margin-right: 1vw;
    font-size: 3vh;
    color: green;
    font-family: 'Times New Roman', Times, serif;
}
.Container{
    grid-row: 2/span 1;
    margin: 2.5vh 2.5vw;
    height: 50vh;
    width: 90vw;
    align-items: center;
    overflow: auto;
}
.Container>span{
    display: inline-block;
}
.TheCart>button{
    margin-top: -2.5vh;
    font-family: "Lucida Handwriting","Papyrus","Monaco";
    display: inline-flex;
    height: 5vh;
    aspect-ratio: 8/3;
    border: transparent;
    background-color: black;
    color: white;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 500ms ease 0s, border-color 500ms ease 0s, color 500ms ease 0s, fill 500ms ease 0s;
    font-size: 2vh;
}
.TheCart>button:hover{
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.16), rgba(255, 255, 255, 0.16)), rgb(30, 30, 30);
    border: transparent;
}
.PurchasedCouponPopping{
    height: 60vw;
    width: 40vw;
}
.PoppingContainer{
    width: 35vw;
}