.Login{
    display: grid;
	height: 100%;
    width: 100%;
    grid-template-columns: 8vh auto;
}
.LogButton{
    grid-row: span 1;
}
.HelloMessage{
    grid-row: span 1;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-decoration: none;
    text-align: left;
    font-size: 2vh;
    padding-left: 2vw;
    padding-top: 2vh;
}
