.Bag {
    height: 100%;
    width: 100%;
    text-decoration: none;
}
.MyCartButton{
    font-family: "Papyrus","Lucida Handwriting","Monaco";
    text-decoration: none;
    display: grid;
    height: 7vh;
    aspect-ratio: 6/5;
    grid-template-areas: "head head"
                         "icon foot";
    grid-template-rows: 50% 50%;
    grid-template-columns: 70% 30%;
    border-radius: 10%;
    margin-top: 1.5vh;
    text-align: center;
    cursor: pointer;
    align-items: center;
    transition: background-color 500ms ease 0s, border-color 500ms ease 0s, color 500ms ease 0s, fill 500ms ease 0s;
    border: transparent;
    background: none;
    color: black;
}
.MyCartButton:hover{
    background: rgb(200, 200, 200);
    border: transparent;
}
.MyCartTitle{
    font-size: 1.5vh;
    grid-area: head;
}
.CartIcon{
    grid-area: icon;
    text-align: right;
}
#PurchaseCounter{
    grid-area: foot;
    font-size: 1.5vh;
    text-align: left;
}