.PoppingForm {
	height: 60vh;
	width: 20vw;
	overflow: hidden;
	background-color: black;
	color: white;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 2vh 2vw;
}
.ChangePassword{
	height: 60vh;
}
.CompanyForm{
	height: 50vh;
}
.ConfirmDelete{
	height: 60vh;
}
.CouponForm{
	height: 60vh;
}
.LoginForm{
	height: 60vh;
}
.ProfileForm{
	height: 60vh;
}
.RegisterForm{
	height: 60vh;
}
.SuccessfullyAdded{
	height: 30vh;
}
.CompanyUpdated{
	height: 20vh;
	font-size: 3vh;
}
.AlreadyBoughtMsg{
	height: 20vh;
	font-size: 3vh;
}
.PoppingFormText{
	background-color: white;
	color: black;
	margin: 2vh 0vw;
	border: none;
	outline: none;
	border-radius: 0.5vh;
}
.PoppingFormButton{
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    border-radius: 3vh;
    text-align: center;
    cursor: pointer;
    align-items: center;
    transition: background-color 500ms ease 0s, border-color 500ms ease 0s, color 500ms ease 0s, fill 500ms ease 0s;
    border: transparent;
    background: none;
    color: black;
}
.PoppingFormButton:hover{
    background: rgb(200, 200, 200);
    border: transparent;
}