.LoginButton {
	height: 100%;
    width: 100%;
}
.LoginButton>button{
    font-family: "Papyrus","Lucida Handwriting","Monaco";
    display: grid;
    height: 7vh;
    aspect-ratio: 6/5;
    border-radius: 10%;
    margin-top: 1.5vh;
    text-align: center;
    cursor: pointer;
    align-items: center;
    transition: background-color 500ms ease 0s, border-color 500ms ease 0s, color 500ms ease 0s, fill 500ms ease 0s;
    border: transparent;
    background: none;
    color: black;
    font-size: 1.5vh;
}
.TheButton:hover{
    background: rgb(200, 200, 200);
    border: transparent;
}