.MyAccount{
    text-align: left;
    margin: 1vh 1vw;
    font-size: 3vh;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.MyAccount>button{
    margin: 1vh 0.5vw;
    background-color: black;
    color: white;
    height: 3vh;
    aspect-ratio: 5/1;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 1.5vh;
    border-radius: 1vh;
    border: transparent;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 500ms ease 0s, border-color 500ms ease 0s, color 500ms ease 0s, fill 500ms ease 0s;
    text-decoration: none;
}
.MyAccount>button:hover{
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.16), rgba(255, 255, 255, 0.16)), rgb(30, 30, 30);
    border: transparent;
}