.HomePage {
	height: 100%;
    width: 100%;
    overflow: hidden;
}
.Bar{
    height: 23vh;
    width: 90vw;
    text-align: left;
    margin: 1vh 1vw;
}
.BarTitle{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 2vh;
}
.CouponBar{
    border: 0.3vh solid black;
    overflow: auto;
    height: 21vh;
    border-radius: 0.5vh;
}
.CouponBar>span{
    display: inline-block;
}