.HeaderLayout {
    height: 100%;
    width: 100%;
	display: grid;
    grid-template-rows: 9vh 6vh;
    grid-template-columns: 25vw 50vw 10vw 15vw;
}
.logo{
    grid-column: 1/span 1;
}
.bag{
    grid-column: 3/span 1;
}
.login{
    grid-column: 4/span 1;
}
.menu{
    display: block;
    grid-column: span 4;
    grid-row: 2;
    background: black;
    color: white;
}