.Menu {
    height: 100%;
    width: 100%;
}
.button{
    font-family: "Lucida Handwriting","Papyrus","Monaco";
    font-size: 2vh;
    display: inline-flex;
    height: 4vh;
    width: 16vh;
    border-radius: 20%;
    border: transparent;
    background-color: black;
    color: white;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 500ms ease 0s, border-color 500ms ease 0s, color 500ms ease 0s, fill 500ms ease 0s;
    text-decoration: none;
}
.button:hover{
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.16), rgba(255, 255, 255, 0.16)), rgb(30, 30, 30);
    border: transparent;
}
.container{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.action{
    color: red;
}
.PoppingCloseButton{
    background: none;
    height: 5vh;
}