.SingleCustomer {
    margin: 0.7vh;
    height: 17vh;
    aspect-ratio: 5/3;
    display: grid;
	border: thin solid rgb(244, 244, 244);
    border-radius: 5%;
    background-color: white;
    color: black;
    grid-template-areas: "name coupons"
                         "email email"
                         "delete update";
    grid-template-columns: 50% 50% ;
    grid-template-rows: 40% 40% 20% ;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    cursor: pointer;
    box-shadow: rgb(0 0 0 / 5%) 0px 0.2vh 0.5vh;
    text-decoration: none;
    overflow: hidden;
}
.SingleCustomer:hover{
    box-shadow: rgb(0 0 0 / 10%) 0px 0.2vh 0.5vh;
}
.CustomerName{
    grid-area: name;
    text-align: left;
    font-size: 2vh;
}

.CustomerCouponsCount{
    grid-area: coupons;
    text-align: center;
    font-size: 2vh;
}
.CustomerEmail{
    grid-area: email;
    text-align: left;
    font-size: 2vh;
}
.DeleteButton{
    grid-area: delete;
    align-items: center;
    justify-items: center;
}
.DeleteButton>button{
    display: inline-flex;
    height: 2vh;
    aspect-ratio: 6/2;
    border-radius: 10%;
    border: transparent;
    background-color: rgb(120, 0, 0);
    color: white;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 500ms ease 0s, border-color 500ms ease 0s, color 500ms ease 0s, fill 500ms ease 0s;
    text-decoration: none;
    font-size: 1vh;
}
.DeleteButton>button:hover{
    background-color: rgb(100, 100, 100);
    border: transparent;
}
.UpdateButton{
    grid-area: update;
    align-items: center;
    justify-items: center;
}
.UpdateButton>button{
    display: inline-flex;
    height: 2vh;
    aspect-ratio: 6/2;
    border-radius: 10%;
    border: transparent;
    background-color: black;
    color: white;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 500ms ease 0s, border-color 500ms ease 0s, color 500ms ease 0s, fill 500ms ease 0s;
    text-decoration: none;
    font-size: 0.8vh;
}
.UpdateButton>button:hover{
    background-color: rgb(100, 100, 100);
    border: transparent;
}