.AboutUs {
    height: 75vh;
    width: 100vw;
	text-align: left;
    font-size: 3vh;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin: 1vh 1vw;
    overflow: hidden;
}
.EntrepreneursContainer {
    height: 50vh;
    width: 95vw;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 50% 50%;
    grid-template-areas: "e1 e2"
                         "e3 e4";
}
.ShiriPage {
    grid-area: e1;
}
.MatanPage {
    grid-area: e2;
}
.RoeiPage {
    grid-area: e3;
}
.TalPage {
    grid-area: e4;
}
.EntrepreneurPage {
    padding: 1vh 1vw;
    height: 10vw;
    width: 27vw;
    background-color: rgb(241, 229, 172);
    border-radius: 2vh;
    box-shadow: 2vh 2vh 4vh black;
    font-size: 2vh;
    display: grid;
    grid-template-rows: 40% 60%;
    grid-template-columns: 40% 60%;
    grid-template-areas: "imag name"
                         "desc desc";
}
.EntrepreneurImage {
    grid-area: imag;
}
.EntrepreneurImage>img{
    display: block;
    max-width: 100%;
    max-height: 100%;
    width: auto;
    height: auto;
}
.EntrepreneurName {
    font-size: 5vh;
    grid-area: name;
}
.EntrepreneurDesc {
    grid-area: desc;
}